.music_container {
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(2, 1fr);
}

.composer {
    font-size: 0.9em;
}

.audio_title {
    font-size: 1.1em;
}

@media (max-width: 700px) {
    .music_container {
        grid-template-columns: repeat(1, 1fr);
      
        width: fit-content;
    }
    .music_container .container {
        justify-content: center;
        align-items: center;
    }
}